<template>
    <div class="home">
        <span>支付成功</span>
        <el-divider direction="vertical"></el-divider>
        <span class="l" @click="tz">点击跳转我的购物</span>
    </div>
  </template>
  
  <script>
  import {mapState,mapMutations} from 'vuex'
    export default {
      computed:{
        ...mapState('modules',['userinfo'])
      },
      data(){
        return{
          
        }
      },
      methods:{
        tz(){
            let pathi='/MyShopping/'+this.userinfo.user
            this.$router.push(pathi)
        }
      },
      created(){
    //   console.log(this.$route.query.out_trade_no);
      console.log(this.userinfo.user);
      if(this.$route.query.out_trade_no != undefined){
        this.$api.getzfbtz({
            out_trade_no: this.$route.query.out_trade_no,trade_no: this.$route.query.trade_no,
        }).then(res=>{
            if(res.data.code == 200){
              // console.log(res.data.code);
              this.$api.getdetails({orderid:this.$route.query.out_trade_no}).then(res=>{
                let goods=res.data.data[0];
                goods['byuser']=this.userinfo.user;
                console.log(goods);
                this.$api.getbygoods(goods).then(res=>{
                  console.log(this.$route.query.out_trade_no );
                })
              })
            }
        });
      }
      }
    }
  </script>
  <style lang="less">
     .l:hover{
        color: greenyellow;
     }
  </style>
  